html,
body {
    height: 100%;
    position: relative;
    // background-color: rgba(0, 177, 197, 0.089)
}

.asyncSelect {
    color: black;
}

.App {
    min-height: 100vh;
    /* will cover the 100% of viewport */
    overflow: hidden;
    display: block;
    background-color: #F2F5F7;
    position: relative;
    padding-bottom: 100px;
    /* height of your footer */
}

.App-auth {
    min-height: 100vh;
    /* will cover the 100% of viewport */
    overflow: hidden;
    display: block;
    background-color: #F2F5F7;
    position: relative;
    // padding-bottom: 100px; /* height of your footer */
}

footer {
    position: absolute;
    bottom: 0;
    width: 100%;
}

.jumbotron {
    border-radius: 15px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 25px;
}

